  width: 100%;
  height: 24px;
  border-top: 8px solid #D87777;
  background-color: #FBE8A4;
  border-bottom: 8px solid #91C8E2; }

.container-wrapper {
  display: block;
  width: 100%; }

.container {
  width: 680px;
  margin-left: auto;
  margin-right: auto; }

.app-content {
  padding: 20px 0px;
  min-height: 240px; }

.app-footer {
  color: #ccc;
  font-size: 13px;
  margin-top: 40px;
  padding: 40px 0px;
  border-top: 1px dashed #ccc; }

.app-footer > a {
  color: #ccc; }

.clearfix:after {
